.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: wheat;
    align-items: center;
    min-width: 40%;
}

.image{
     border-radius: 100%;
     width: 300px;
     height: 300px;
}